.Header1 {
  display: block;
  background-image: url(./resources/open-book.jpg);
  min-height: 100%;
  min-width: 600px;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  background-position: center;
  z-index: -1;
  background-repeat: unset;
}
.poster {
  display: flex;
  padding: 1em 2em;
}
.social-logos {
  display: none;
}
.social-logos > a {
  margin: 0.7em;
}
.poster > a > img {
  width: 50px;
  height: 50px;
}
.social-logos > a > img {
  width: 50px;
  height: 50px;
}
.Navbar {
  padding: 1em;
  background-color: rgb(240, 255, 255, 0.5);
  font-size: large;
}
.Navbar > nav > a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: white;
  margin: 2em;
}

.Navbar > nav > a {
  text-decoration: none;
  color: white;
}
.Herotext {
  display: block;
  color: white;
  margin: 3em 1em 1em 1em;
  width: 95vw;
}
.Herotext > h1 {
  font-weight: 600;
  color: #fff;
  font-size: 40px;
  line-height: 1.2;
}
.textSpan {
  font-weight: 700;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  display: inline-block;
  letter-spacing: 2px;
  margin-bottom: 5px;
  position: relative;
  z-index: 0;
}

.Herotext > button {
  padding: 1em 2em;
  font-weight: 600;
  border: none;
  width: 92vw;
}

.Benifits {
  display: flex;
  z-index: 1;
  background-color: white;
  padding: 1em;
}
.benifits-img {
  display: none;
}
.benifits-content {
  padding: 1em;
}
.benifits-content span {
  color: green;
  font-weight: 700;
}
.benifits-content > div {
  display: flex;
}
.benifits-content img {
  margin: 2em;
  width: 50px;
  height: 50px;
}

.Figures {
  background: url(./resources/books-figure-section.jpg);
  padding: 1em;
  color: rgb(255, 60, 60);
}

.figuresData {
  display: flex;
  /* justify-content: center; */
  margin: 1em;
}

.figuresData img {
  margin: 1em;
}

.figuresData h1 {
  font-weight: 700;
}

.figuresData p {
  margin-top: -1em;
  font-weight: 500;
}

.Footer {
  padding: 0.5em;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
}

.footer-columns {
  margin: 0.5em 2em;
}

.footer-social-icons > a > img {
  height: 50px;
  width: 50px;
  margin: 0.5em;
}
.footerLogo {
  height: 60px;
  width: 60px;
}
.exploreLinks,
.linksCol,
.questionsCol {
  display: flex;
  flex-direction: column;
  /* padding: 0.5em; */
}

.exploreLinks > a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
}
.linksCol > a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
}
.questionsCol > a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
}

.Courses {
  background-color: white;
  text-align: center;
}
.Courses span {
  color: green;
  font-weight: 700;
  font-size: x-large;
  display: block;
  margin: 2em;
}
.Courses h2 {
      font-size: xxx-large;
    margin: 0 .5em;
}

.course-item {
  margin: 1em;
  background-color: rgb(246, 246, 246);
  padding: 1em;
  border-radius: 5px;
}

/* tablets */
@media only screen and (min-width: 600px) {
  .Navbar {
    padding: 1em;
    background-color: rgb(240, 255, 255, 0.5);
    font-size: large;
  }
  .Navbar > nav > a {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: white;
    margin: 2em;
  }

  .Navbar > nav > a {
    text-decoration: none;
    color: white;
  }
  .Herotext > button {
    width: 95vw;
  }
  .Figures {
    background: url(./resources/books-figure-section.jpg);
    padding: 3em;
    color: rgb(255, 60, 60);
  }
  .figuresData {
    display: flex;
    justify-content: center;
    margin: 2em;
  }
  .Figures {
    display: grid;
    grid-template-columns: auto auto;
  }
}

/* desktops */
@media only screen and (min-width: 768px) {
  .Header1 {
    background-image: url(./resources/open-book.jpg);
    min-height: 100%;
    min-width: 768px;
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;
    left: 0;
    background-position: center;
  }
  .poster {
    display: flex;
    justify-content: space-between;
    padding: 1em 6em;
  }
  .poster > a > img {
    width: 50px;
    height: 50px;
  }
  .social-logos {
    display: block;
  }
  .social-logos > a > img {
    width: 50px;
    height: 50px;
  }
  .Navbar {
    background-color: rgb(240, 255, 255, 0.5);
    margin: 1em;
  }
  .Navbar > nav {
    display: flex;
    flex-direction: initial;
  }
  .Navbar > nav > a {
    margin: 1em 2em;
  }
  .Herotext > button {
    width: auto;
    padding: 1em 2em;
  }
  .Herotext {
    margin: 3em 6em;
    width: 90vw;
  }
  .Herotext > h1 {
    font-weight: 600;
    color: #fff;
    font-size: 40px;
    line-height: 1.2;
    margin-bottom: 1em;
    width: 40%;
  }
  .Herotext > p {
    font-weight: 700;
    color: rgba(255, 255, 255, 0.7);
    font-size: 14px;
    display: block;
    letter-spacing: 2px;
    margin-bottom: 3em;
    font-family: inherit;
    width: 40%;
  }

  .Benifits {
    display: flex;
    z-index: 1;
    background-color: rgb(255, 255, 255);
  }
  .benifits-img {
    background-image: url(./resources/woman.jpg);
    display: block;
    width: 100%;
    height: auto;
    z-index: 3;
  }
  .Benifits {
    display: flex;
    z-index: 1;
    background-color: white;
    padding: 1em;
  }
  .benifits-content {
    padding: 3em;
  }
  .benifits-content span {
    color: green;
    font-weight: 700;
  }
  .benifits-content > div {
    display: flex;
  }
  .benifits-content img {
    margin: 2em;
    width: 50px;
    height: 50px;
  }
  .Figures {
    background: url(./resources/books-figure-section.jpg);
    padding: 3em;
    color: rgb(255, 60, 60);
  }
  .figuresData {
    display: flex;
    justify-content: center;
    margin: 2em;
  }
  .Footer {
    padding: 0.5em;
    background-color: whitesmoke;
    display: grid;
    grid-template-columns: auto auto auto auto;
  }
}
